.reply-editor {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 16px;
  background: white;
  border-radius: 8px;
  border: 1px solid #e0e0e0;

  form {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  .editor-header {
    h4 {
      margin: 0;
      font-size: 16px;
      font-weight: 600;
      color: #333;
    }
  }

  .markdown-toolbar {
    display: flex;
    align-items: center;
    gap: 4px;
    padding: 8px;
    background: #f5f5f5;
    border-radius: 4px;
    border: 1px solid #e0e0e0;
    overflow-x: auto;

    button {
      &[mat-icon-button] {
        height: 36px;
        width: 36px;
        line-height: 36px;

        &:hover {
          background: #ede7f6;
        }
      }
    }
  }

  .form-group {
    display: flex;
    flex-direction: column;
    gap: 8px;

    label {
      font-weight: 500;
      font-size: 14px;
      color: #333;
    }

    .reply-textarea {
      padding: 12px;
      border: 1px solid #ddd;
      border-radius: 4px;
      font-family: 'Segoe UI', sans-serif;
      font-size: 14px;
      resize: vertical;

      &:focus {
        outline: none;
        border-color: #1976d2;
        box-shadow: 0 0 0 3px rgba(25, 118, 210, 0.1);
      }
    }

    .char-count {
      font-size: 12px;
      color: #999;
      text-align: right;
    }

    mat-checkbox {
      margin-bottom: 8px;

      .hint {
        display: block;
        font-size: 12px;
        color: #999;
        margin-top: 4px;
        margin-left: 32px;
      }
    }

    .score-input-group {
      padding: 12px;
      background: #f9f9f9;
      border-radius: 4px;
      border-left: 3px solid #1976d2;

      label {
        margin-bottom: 8px;
      }

      .score-field {
        width: 150px;
      }
    }
  }

  .button-group {
    display: flex;
    gap: 8px;
    margin-top: 8px;

    button {
      &[mat-raised-button] {
        min-width: 100px;
      }
    }
  }
}
